<template>
  <div id="my-master-app" class="my-master-app">
    <keep-alive>
      <router-view v-if="keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!keepAlive"></router-view>
  </div>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-master-app
   */
  import Scale from '$ui/utils/scale'

  export default {
    name: 'MyMasterApp',
    mixins: [Scale],
    provide() {
      return {
        // 
        masterApp: this
      }
    },
    data() {
      this.actions = null
      return {}
    },
    /**
     * 
     * @member props
     * @property {Object} [matchApp] 
     * @property {Object} [state] 
     * @property {Function} [initGlobalState]  qiankun 
     */
    props: {
      matchApp: {
        type: Object
      },
      state: {
        type: Object
      },
      initGlobalState: Function
    },
    computed: {
      /**
       * 
       * @property {boolean} keepAlive
       */
      keepAlive() {
        const matched = this.$route.matched[0]
        if (!matched) return false
        return !!matched.meta.keepAlive
      }
    },
    methods: {
      /**
       * 
       * @method initState
       * @param {object} state
       */
      initState(state) {
        this.offStateChange()
        if (this.initGlobalState) {
          this.actions = this.initGlobalState(state)
        }
      },
      /**
       * 
       * @method setState
       * @param {object} state
       */
      setState(state) {
        this.actions && this.actions.setGlobalState(state)
      },
      /**
       * 
       * @method onStateChange
       * @param {function} callback : state,  prev
       * @param {boolean} [fireImmediately] 
       */
      onStateChange(callback, fireImmediately) {
        this.actions && this.actions.onGlobalStateChange(callback, fireImmediately)
      },
      /**
       * 
       * @method onStateChange
       */
      offStateChange() {
        this.actions && this.actions.offGlobalStateChange()
      }
    },
    created() {
      this.initState(this.state || {})
    },
    beforeDestroy() {
      this.offStateChange()
    }
  }
</script>

